<template>
  <div id="label">
    <el-tag v-for="(item,index) in tag" :type="tagType(index)" :key="index">{{item}}</el-tag>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tag: [
        "Linux基础",
        "Photoshop",
        "php基础",
        "mysql数据库",
        "nginx配置",
        "webpack4配置",
        "git协作开发",
        "ajax与JSON请求",
        "bootstrap",
        "vux-UI",
        "element-UI",
        "koa2 node框架",
        "weui ",
        "wux-UI",
        "color-UI"
      ]
    };
  },
  methods: {
    tagType: function(number) {
      let tagColor = number % 4;
      switch (tagColor) {
        case 0:
          return "success";
          break;
        case 1:
          return "info";
          break;
        case 2:
          return "warning";
          break;
        case 3:
          return "danger";
          break;
        default:
          return "success";
      }
    }
  }
};
</script>
<style scoped>
#label{
  text-align: center
}
.el-tag {
  margin: 5px 10px;
}
</style>


