<template>
  <div id="index">
    <el-image class="bgImage" style="width: 100%; height: 100%" :src="bgImage" fit="cover"></el-image>
    <div class="showBox">
      <div class="icon"><img :src="icon" alt="" srcset=""></div>
      <div class="name">BoredWait 个人博客</div>
      <div class="classify">
        <router-link to="/resume">个人简历</router-link>
        <router-link to="/news">时事文章</router-link>
        <router-link to="/article">我的文章</router-link>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      bgImage: "",
      icon:""
    };
  },
  created() {
    this.icon = require("../assets/images/icon.jpg");
    if (this._isMobile()) {
      this.bgImage = require("../assets/images/mobileBg.jpg");
    } else {
      this.bgImage = require("../assets/images/code.jpg");
    }
  },
  mounted() {
  },
  methods: {
    _isMobile() {
      let flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      );
      return flag;
    }
  }
};
</script>


<style scoped>
#index {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.bgImage {
  position: absolute;
  filter: blur(1px);
}
.showBox {
  text-align: center;
  z-index: 10;
}

.icon {
  width: 80px;
  height: 80px;
  margin: 0 auto;
  border: solid 6px #fff;
  border-radius: 50%;
  overflow: hidden;
}
.icon img {
  width: 100%;
  transition: all ease 2s;
  -webkit-transition: all ease 2s; /* Safari */
}
.icon img:hover {
  transform: rotate(370deg);
  -ms-transform: rotate(370deg); /* IE 9 */
  -moz-transform: rotate(370deg); /* Firefox */
  -webkit-transform: rotate(370deg); /* Safari 和 Chrome */
  -o-transform: rotate(370deg); /* Opera */
}
.name {
  font-family: "MyFont";
  font-size: 2em;
  color: rgb(255, 255, 255);
  margin-bottom: 30px;
}
.classify {
