  height: 2em;
  line-height: 2em;
}
.classify a {
  margin: 0 20px;
}
.classify a,
.classify a.router-link-active {
  color: rgb(255, 255, 255);
  text-shadow: 1px 2px 2px rgb(175, 175, 175);
}
.classify a:hover {
  text-shadow: 3px 2px 3px rgb(173, 240, 252);
  text-decoration-line: underline;
  font-size: 1.11em;
  margin: 0 16px;
}
</style>
