<template>
  <div id="juejin">
    <div v-for="(item,index) in articleList" :key="index">
      <div class="articleBox">
        <div>
          <el-tag size="mini" v-for=" (itema,indexa) in item.node.tags" :key="indexa">{{itema.title}}</el-tag>
        </div>
        <div class="articleTitle">
          <a :href="item.node.originalUrl">{{item.node.title}}</a>
        </div>
        <div class="tagList">
          <span><i class="el-icon-time"></i> {{timeFormat(item.node.createdAt)}}</span>
          <el-divider direction="vertical"></el-divider>
          <span><i class="el-icon-user"></i> {{item.node.user.username}}</span>
          <el-divider direction="vertical"></el-divider>
          <span><i class="el-icon-view"></i> {{item.node.likeCount}}</span>
          <el-divider direction="vertical"></el-divider>
          <span><i class="el-icon-chat-dot-square"></i> {{item.node.commentsCount}}</span>
        </div>
      </div>
      <el-divider></el-divider>
    </div>
    <div class="loading"> <i class="el-icon-loading"></i> 加载更多数据</div>
  </div>
</template>

<script>
import {
  juejinData,
  juejinDataAdd
} from "../../assets/js/api.js";
export default {
  data() {
    return {
        showId : 1,
      backTopChange: true,
      dateNumber: 0,
      articleList: [],
      juejinEndCursor: ""
    };
  },
  methods: {
    timeFormat: time => {
      let d = new Date(time);
      let dnumber = new Date();
      let numberDate = dnumber.getTime() - d.getTime();
      let da = new Date(numberDate);
      if (da.getFullYear() - 1970) {
        return da.getFullYear() - 1970 + "年前";
      } else if (da.getMonth()) {
        return da.getMonth() + "月前";
      } else if (da.getDate() - 1) {
        return da.getDate() - 1 + "天前";
      } else if (da.getHours() - 8) {
        return da.getHours() - 8 + "小时前";
      } else if (da.getMinutes()) {
        return da.getMinutes() + "分钟前";
      } else if (da.getSeconds()) {
        return da.getSeconds() + "秒前";
      }
    },
    juejinAdd: async function(num) {
      let lists = await juejinDataAdd(num);
      lists.edges.forEach((item, index) => {
        this.articleList.push(item);
      });
      this.juejinEndCursor = lists.pageInfo.endCursor;
    }
  },
  created() {
    const that = this;
    window.onscroll = function() {
      //变量scrollTop是滚动条滚动时，距离顶部的距离
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop; //变量windowHeight是可视区的高度
      let windowHeight =
        document.documentElement.clientHeight || document.body.clientHeight; //变量scrollHeight是滚动条的总高度
      let scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight; //滚动条到底部的条件
      let backTopShow = scrollTop >= 600 ? true : false;
      if (that.backTopChange != backTopShow) {
        that.backTopChange = backTopShow;
        that.$emit("backTopShow", backTopShow);
      }

      if (scrollTop + windowHeight == scrollHeight) {
        //写后台加载数据的函数
        that.juejinAdd(that.juejinEndCursor);
      }
    };
  },
  mounted: async function() {
  
        let lists = await juejinData();
        this.articleList = lists.edges;
        this.juejinEndCursor = lists.pageInfo.endCursor;
    // let lists = await juejinData();
    // this.articleList = lists.edges;
    // this.juejinEndCursor = lists.pageInfo.endCursor
  }
};
</script>
<style scoped>
.articleBox {
  margin: 0 20px;
}
.articleTitle {
  margin: 16px 0;
  font-size: 20px;
}
.articleTitle a.router-link-active {
  color: -webkit-link;
}
.el-tag {
  margin: 2px 5px;
}

.loading {
  margin: 20px;
  text-align: center;
  font-size: 18px;
}
.loading i {
  font-size: 20px;
}
</style>

