<template>
  <div id="news">
    <div class="title">时事新闻</div>
    <el-tabs v-model="activeName">
      <el-tab-pane v-for="(item,index) in newsType" :key="index" :name="item.name">
        <span slot="label"><i :class="item.icon"></i> {{item.title}}</span>
        <ArticleList v-if="activeName == item.name" v-on:backTopShow="ShowChild" :id="item.id" />
      </el-tab-pane>
    </el-tabs>
    <div class="topBox" v-if="backTopShow" @click="backTop">
      <i class="el-icon-top"></i>
    </div>
  </div>
</template>
<script>
import ArticleList from "../components/news/ArticleList";

export default {
  data() {
    return {
      backTopShow: false,
      activeName: "juejin",
      newsType: [
        { name: "juejin", icon: "el-icon-date", title: "掘金前端", id: 1 },
        { name: "csdn", icon: "el-icon-date", title: "csdn文章", id: 2 }
      ]
    };
  },
  components: {
      ArticleList
  },
  computed:{
      activeShow:function(){
          this.activeName
      }
  },
  mounted() {},
  methods: {
    ShowChild: function(data) {
        this.backTopShow = data;
    },
    backTop() {
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let scrolla = setInterval(function() {
        scrollTop -= 80;
        document.documentElement.scrollTop = scrollTop;
        if (scrollTop < 80) {
          document.documentElement.scrollTop = 0;
          clearInterval(scrolla);
        }
      }, 1);
    }
  }
};
</script>
<style >
#news .title {
  text-align: center;
  font-size: 26px;
  font-family: "MyFont";
  color: rgb(68, 68, 68);
  text-align: center;
}
#news .el-tabs__nav-wrap {
  padding: 0 10px !important;
}
#news .el-tabs__nav-scroll {
  /* overflow-x: hidden !important; */
  overflow-x: scroll !important;
  overflow-y: hidden !important;
}
#news .el-tabs__nav-scroll::-webkit-scrollbar {
  display: none;
}
#news .el-tabs__nav-prev,
#news .el-tabs__nav-next {
  display: none !important;
}
.topBox {
  width: 40px;
  height: 40px;
  position: fixed;
  bottom: 20px;
  right: 10px;
  font-size: 30px;
  font-weight: bold;
  background: rgba(45, 107, 189, 0.5);
  border-radius: 20px;
  text-align: center;
  line-height: 40px;
  color: #fff;
}
</style>


