<template>
  <div id="article">
    <div class="title">我的文章</div>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="18" :xs="23">
        <el-row :gutter="10" justify="center">
          <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16">
            <div class="showArticle">
              <el-card class="box-card articleBox" v-for="(item,index) in myArticle" :key="index">
                <el-row :gutter="22">
                  <el-col :span="16">
                    <div>
                      <el-tag v-for="(itema,indexa) in item.tags" :key="indexa" :type="tagType(indexa+index)" size="mini">{{itema.title}}</el-tag>
                    </div>
                    <div class="titleBox"> <a :href="item.originalUrl">{{item.title}}</a> </div>
                  </el-col>
                  <el-col :span="8">
                    <el-image :src="item.screenshot" style="width: 100%; height: 5rem" fit="cover">
                      <div slot="error" class="image-slot">
                        <i class="el-icon-picture-outline"></i>
                      </div>
                    </el-image>
                  </el-col>
                </el-row>

                <div class="tagList">
                  <span><i class="el-icon-time"></i> {{timeFormat(item.createdAt)}}</span>
                  <el-divider direction="vertical"></el-divider>
                  <span><i class="el-icon-view"></i> {{item.viewsCount}}</span>
                  <el-divider direction="vertical"></el-divider>
                  <span><i class="el-icon-thumb"></i> {{item.collectionCount}}</span>
                  <el-divider direction="vertical"></el-divider>
                  <span><i class="el-icon-discount"></i> {{item.user.jobTitle}}</span>

                </div>
              </el-card>
            </div>
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
            <div class="showAbout">
              <el-card class="box-card articleBox">

              </el-card>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

  </div>
</template>
<script>
import { juejinBlog } from "../assets/js/api";
export default {
  data() {
    return {
      myArticle: []
    };
  },
  computed: {
    
  },
  methods: {
      tagType: function(number) {
      let tagColor = number % 4;
      switch (tagColor) {
        case 0:
          return "success";
          break;
        case 1:
          return "info";
          break;
        case 2:
          return "warning";
          break;
        case 3:
          return "danger";
          break;
        default:
          return "success";
      }
    },
    timeFormat: time => {
      let d = new Date(time);
      let dnumber = new Date();
      let numberDate = dnumber.getTime() - d.getTime();
      let da = new Date(numberDate);
      if (da.getFullYear() - 1970) {
        return da.getFullYear() - 1970 + "年前";
      } else if (da.getMonth()) {
        return da.getMonth() + "月前";
      } else if (da.getDate() - 1) {
        return da.getDate() - 1 + "天前";
      } else if (da.getHours() - 8) {
        return da.getHours() - 8 + "小时前";
      } else if (da.getMinutes()) {
        return da.getMinutes() + "分钟前";
      } else if (da.getSeconds()) {
        return da.getSeconds() + "秒前";
      }
    }
  },
  mounted: async function() {
    let lists = await juejinBlog();
    console.log(lists.entrylist);
    this.myArticle = lists.entrylist;
  }
};
</script>

<style scoped>
.title {
  text-align: center;
  font-size: 26px;
  font-family: "MyFont";
  color: rgb(68, 68, 68);
  text-align: center;
}
.titleBox{
    overflow : hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
a{
  color: rgb(70, 67, 228);
}
.articleBox {
  margin: 10px 0;
}

</style>

