<template>
  <div id="articleList">
    <JueJin v-if="id == 1" v-on:backTopShow="ShowChild" />
    <Csdn v-if="id == 2" v-on:backTopShow="ShowChild" />
  </div>
</template>

<script>
import JueJin from "./JueJin";
import Csdn from "./Csdn";
export default {
  props: ["id"],
  data() {
    return {
      backTopChange: true
    };
  },
  components: {
    JueJin,
    Csdn
  },
  methods: {
    ShowChild: function(data) {
        this.$emit("backTopShow", data);
    },
  }
};
</script>